
.bigcontainer{
    display: flex;
    justify-content: center; 
    align-items: center;
    background-color: rgb(160, 47, 47);
    height: 580px;

}
.bigcontainer .bikebox{
    border-radius: 15px;
    margin-top: 120px;
    margin-right: 70px;
    height: 300px;
    width: 300px;
    background-color: rgb(66, 3, 3);
}
.bigcontainer .carbox{
    border-radius: 15px;
    margin-top: 120px;
    margin-left: 140px;
    height: 300px;
    width: 300px;
    background-color:rgb(66, 3, 3);
}
.bigcontainer .bikebox .headof{
    display: flex;
    justify-content: center; 
    align-items: center;
    color: aliceblue;

    border-top-right-radius: 15px;
    border-top-left-radius: 15px;

    font-size: 25px;
    height: 20%;
    background-color: orange;
}
.bigcontainer .carbox .headof{
    display: flex;
    justify-content: center; 
    align-items: center;
    color: aliceblue;

    border-top-right-radius: 15px;
    border-top-left-radius: 15px;

    font-size: 25px;
    height: 20%;
    background-color: orange;
}
.img-sec{
    display: flex;
    justify-content: center; 
    align-items: center;
    height: 65%;
    width: 100%;
    
    
}
.img-sec .photo{
    width: 70%;
    height: 80%;
    border-radius: 25px;

}
.down-sec{
    display: flex;
    height:15% ;
}
.down-sec .ts1{
    display: flex;
    justify-content: center; 
    align-items: center;
    width: 50%;
    height: 100%;

    font-size: 25px;
    color: white;
    background-color: orange;

    
    border-bottom-left-radius: 15px;
}
.down-sec .ts2{
    display: flex;
    justify-content: center; 
    align-items: center;
    width: 50%;
    height: 100%;

    font-size: 25px;
    color: white;
    background-color: orange;
    border-bottom-right-radius: 15px;
}
