.App {
  text-align: center;
  background-color: rgb(160, 47, 47);
}
.App-header {
  height: 250px;
  background-image: url(./parking.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.App-header1 {
  height: 250px;
  background-image: url(./bikepark.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 250px;
}
h1 {
  font-family: "Faster One", cursive;
  font-size: 60px;
  font-weight: lighter;
  color: whitesmoke;
  padding-top: 40px;
  text-align: center;
}

.Line {
  display: block;
  height: 1px;
  border: 0px;
  border-top: 5px solid rgb(77, 74, 74);
}

#Parking {
  margin: 30px 20px 5px;
}

.Slots {
  display: inline-block;
  height: 180px;
  width: 15%;
  border: 2px solid rgb(59, 2, 2);
  background-color: white;
  margin: 10px 20px;
  border-radius: 35px;
}
.Slot-head {
  height: 20%;
  background-color: orange;
  border-top-right-radius: 35px;
  border-top-left-radius: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  color: aliceblue;
}
.chkbx {
  height: 10px;
}