.bikebox{
    width: 15%;
    height: 180px;
    margin: 25px;
    background-color: rgb(66, 3, 3);
    border-radius: 35px;
}
.bikebox .header1{
    color: white;
    
    font-size: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20%;
    background-color: orange;
    border-top-right-radius: 35px;
    border-top-left-radius: 35px;
}
.bikebox .header1 .sign{
    margin-left: 15px;
    font-size: 44px;
    margin-bottom: 53px;
}
.bikebox .img-box{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60%;
    width: 100%;

}
.bikebox .img-box img{
    height: 80%;
    width: 50%;
    border-radius: 15px;
}
.bikebox .box-foot{
    color: white;
    
    font-size: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20%;
    background-color: orange;
    border-bottom-right-radius: 35px;
    border-bottom-left-radius: 35px;

}